import React, { Component } from "react";
import { Route } from "react-router-dom";

// Components
import Developer from "./authors/Developer";
import Redactor from "./authors/Redactor";
import Writer from "./authors/Writer";
import Default from "./authors/Default";

export class AuthorsPage extends Component {
  constructor(props) {
    // Gets only location from Main.jsx
    super();

    this.state = {};
    this.state.location = props.location;
  }

  render() {
    return (
      <React.StrictMode>
        <Route
          exact
          path={`/${this.state.location.authors}/id=1`}
          component={Developer}
        />
        <Route
          exact
          path={`/${this.state.location.authors}/id=2`}
          component={Redactor}
        />
        <Route
          exact
          path={`/${this.state.location.authors}/id=3`}
          component={Writer}
        />
        <Route
          exact
          path={`/${this.state.location.authors}`}
          render={(props) => <Default {...props} />}
        />
      </React.StrictMode>
    );
  }
}
